<template>
  
 
 
    <div style="margin-top:10px;">
    
    <v-data-table
        :headers="headers"
        :items="users"
        :search="search"
        sort-by="calories"
        class="elevation-1"
        
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
           
            <v-spacer></v-spacer>
            <!-- <v-text-field
              dense
              v-model="search"
              append-icon="search"
              label="Search"
              solo
              class="mt-5 mr-5"
            ></v-text-field> -->
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  :disabled="disabled"
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >New Transaction</v-btn>
              </template>
              <v-card>
                <v-card-title class="headline blue lighten-2 white--text" primary-title>
                  <span class="headline">{{ formTitle }} For {{estateName}}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="editedUser.details" label="Transaction Details"></v-text-field>
                      </v-col>
                       <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="amount" label="Transaction Amount" placeholder="100" type="number" prefix="$"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="12" md="12">
                        <v-select
                          v-model="editedUser.type"
                          item-text="name"
                          item-value="id"
                          class="text-left"
                          :items="tranTypeData"
                          label="Select Transaction Type"
                        ></v-select>
                      </v-col>
                     
                      
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.estateStatus="{ item }">
            {{ estateStatus(item.estateStatus) }}
        </template>
         <template v-slot:item.estateValue="{ item }">
            ${{ parseFloat(item.estateValue).toFixed(2) }}
        </template>
         <template v-slot:item.createdAt="{ item }">
            {{new Date(item.createdAt).toLocaleString('en-GB', { timeZone: 'UTC' }) }}
        </template>
         <template v-slot:item.updatedAt="{ item }">
            {{new Date(item.updatedAt).toLocaleString('en-GB', { timeZone: 'UTC' }) }}
        </template>
        <!-- <template v-slot:item.actions="{ item }"> -->
          <!-- <v-icon
            color="blue"
            class="mr-2"
            @click="editEstate(item)"
          >
            edit
          </v-icon> -->
<!--           
          <v-icon
            color="red"
            class="mr-2"
            @click="deleteItem(item)"
          >
            delete
          </v-icon> -->
        
        <!-- </template> -->
        <template v-slot:no-data>
          <h2>No Estate Record Found</h2>
        </template>
      </v-data-table>


       <v-data-table
        :headers="headersBene"
        :items="transactions"
        :search="searchBene"
        sort-by="financialId"
        class="elevation-1"
        
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
            
            <v-spacer></v-spacer>
            <v-text-field
              dense
              v-model="searchBene"
              append-icon="search"
              label="Search Transaction"
              solo
              class="mt-5 mr-5"
            ></v-text-field>
           
          </v-toolbar>
        </template>
        <template v-slot:item.debtiamount="{ item }">
            {{parseFloat(item.debtiamount).toFixed(2) }}
        </template>
         <template v-slot:item.creditamount="{ item }">
            {{ parseFloat(item.creditamount).toFixed(2) }}
        </template>
         
         <template v-slot:item.createdAt="{ item }">
            {{new Date(item.createdAt).toLocaleString('en-GB', { timeZone: 'UTC' }) }}
        </template>
         <template v-slot:item.updatedAt="{ item }">
            {{new Date(item.updatedAt).toLocaleString('en-GB', { timeZone: 'UTC' }) }}
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon
            v-if="userType <= 2"
            color="blue"
            class="mr-2"
            @click="editItem(item)"
          >
            edit
          </v-icon>
          
          <v-icon
            v-if="userType == 1"
            color="red"
            class="mr-2"
            @click="deleteItem(item)"
          >
            delete
          </v-icon>
        
        </template>
        <template v-slot:no-data>
         <h2>No transactions found for this estate</h2>
        </template>
      </v-data-table>



      <v-dialog v-model="dialogEstate" max-width="500px">
             
              <v-card>
                <v-card-title class="headline green lighten-2 white--text" primary-title>
                  <span class="headline">{{ formTitleEstate }} For {{firstName}} {{lastName}}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="estate.estateName" label="Estate Name" :placeholder="`${firstName} ${lastName} Trust`"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="estate.estateValue" label="Estate Value" type="number" placeholder="0.00" prefix="$"></v-text-field>
                      </v-col>
                     
                      
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeEstate">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="saveEstate">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
    </div>
    
</template>
<script>
export default {
  props:{
    personId:Number
  },
data () {
    return {
      nodeApi: '',
      page : 'Main Page',
      dialog: false,
      dialogEstate: false,
      search: '',
      searchBene: '',
      firstName:'',
      lastName:'',
      estateName:'',
      confirm_password: '',
      tranTypeData: [{id:1,name:'Debit Estate'},{id:2,name:'Credit Estate'}],
      userStatusData: [{id:1,name:'Active'},{id:2,name:'Inactive'}],
      headers: [
        { text: 'Estate ID',value: 'estateId'},
        { text: 'Estate Name', value: 'estateName' },
        { text: 'Full Estate Value', value: 'estateValue' },
        { text: 'Estate Owner', value: 'estateOwner' },
        { text: 'Estate Status', value: 'estateStatus' },
        { text: 'Date Registered', value: 'createdAt' },
        { text: 'Date Modified', value: 'updatedAt' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
       headersBene: [
        { text: 'Transaction ID',value: 'financialId'},
        { text: 'Date Registered', value: 'createdAt' },
        { text: 'Date Modified', value: 'updatedAt' },
        { text: 'Transaction Details', value: 'details' },
        { text: 'Debit', value: 'debitamount' },
        { text: 'Credit', value: 'creditamount' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      users: [],
      person:[],
      amount: '',
      editedIndex: -1,
      editedUser: {
        estateId:'',
        details: '',
        debitamount: '',
        creditamount:'',
        type: '',
      },
      defaultUser: {
       
        estateId:'',
        details: '',
        debitamount: '',
        creditamount:'',
        type: '',
        
      },
      estateIndex: -1,
      estate: {
        estateName: '',
        estateValue: '',
        deathRecordId:''
      },
      defaultEstate: {
      
        estateName: '',
        estateValue: '',
        deathRecordId:''
      },
      disabled:true,
      transactions:[],
      disabledBene:false,
       userType:null
      
    }
  },
  computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Transaction' : 'Edit Transaction'
      },
      formTitleEstate () {
        return this.estateIndex === -1 ? 'New Estate' : 'Edit Estate'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
     
    },
     mounted(){
    this.userType = this.$session.get('user').usertype
      this.getPerson(this.personId)
      this.initialize(this.personId)
    },

    created () {
      //this.initialize()
      
    },

    methods: {
      newEstate(){
        this.dialogEstate = true
      },
      initialize (id) {
        this.axios.post(this.nodeApi + '/getEstate',{personId:id}).then((response) => {
         this.users = response.data
         if(this.users.length < 1){
           this.disabled = true
         }else{
           this.disabled = false
           this.estateName = this.users[0].estateName
           this.initializeBene(this.users[0].estateId)
         }
        }).catch(err =>{
         console.log(err)
        })
       
      },

      initializeBene (id) {
        this.axios.post(this.nodeApi + '/getTransactions',{estateId:id}).then((response) => {
         this.transactions = response.data
         
        }).catch(err =>{
         console.log(err)
        })
       
      },
       getPerson (id) {
        this.axios.post(this.nodeApi + '/getPerson',{personId:id}).then((response) => {
         this.person = response.data
         if(this.person.length > 0){
           this.firstName = this.person[0].firstName
           this.lastName = this.person[0].lastName
         }
        // console.log(this.person)
        }).catch(err =>{
         console.log(err)
        })
       
      },
      estateStatus(item){
        if(item == 0){
          return 'Pending ED 15'
        }
        if(item == 1){
          return 'Pending ED 16'
        }
      },

      editItem (item) {
        this.editedIndex = this.transactions.indexOf(item)
        this.editedUser = Object.assign({}, item)
        
        if(this.editedUser.debitamount > 0){
          this.amount = this.editedUser.debitamount
        }else{
          this.amount = this.editedUser.creditamount
        }
        this.dialog = true
      },

      editEstate (item) {
        this.estateIndex = this.users.indexOf(item)
        this.estate = Object.assign({}, item)
        this.dialogEstate = true
      },

      deleteItem (item) {
        const index = this.transactions.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.transactions.splice(index, 1)
      },

       deleteEstate (item) {
        const index = this.transactions.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.transactions.splice(index, 1)
      },

      close () {
        this.dialog = false
        
        this.$nextTick(() => {
          this.editedUser = Object.assign({}, this.defaultUser)
          this.editedIndex = -1
        })
        
      },

      closeEstate () {
        this.dialogEstate = false
       
        this.$nextTick(() => {
          this.estate = Object.assign({}, this.defaultEstate)
          this.estateIndex = -1
        })
        
      },

      save () {
        
       
        if (this.editedIndex > -1) {
         if(this.editedUser.type == 1){
            this.editedUser.debitamount = parseFloat(this.amount).toFixed(2)
            this.editedUser.creditamount = 0
          }else{
             this.editedUser.debitamount = 0
            this.editedUser.creditamount = parseFloat(this.amount).toFixed(2)
          }
        let data = {
         
          details: this.editedUser.details,
          debitamount: this.editedUser.lastName,
          creditamount: this.editedUser.nationalId,
          financialId: this.editedUser.financialId
          
        }
        
          this.axios.post(this.nodeApi + '/updateTransaction',data).then((response) => {
         
           if(response.data.affectedRows > 0){
             this.$swal.fire({
              type: 'success',
              title: 'Transaction Updated',
              text: 'Your transaction was successfully updated.'
            })
           
            this.initialize(this.personId)
           }else{
             this.$swal.fire({
              type: 'error',
              title: 'Updating Transaction Failed',
              text: response.data.originalError.info.message
            })
           }
           
          }).catch(err =>{
            this.$swal.fire({
              type: 'error',
              title: 'Updating Transaction Failed',
              text: err
            })
          })
          this.close()
        } else {
          if(!this.editedUser.details){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Transaction Failed',
              text: 'Please enter valid transaction details'
            })
          }else if(!this.amount){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Transaction Failed',
              text: 'Please enter a valid transaction amount'
            })
          }else if(Number(this.amount) < 1){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Transaction Failed',
              text: 'Please enter a valid transaction amount greater than 0.'
            })
          }else if(!this.editedUser.type){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Transaction Failed',
              text: 'Please select a valid transaction type'
            })
          }else{

          if(this.editedUser.type == 1){
            this.editedUser.debitamount = parseFloat(this.amount).toFixed(2)
            this.editedUser.creditamount = 0
          }else{
             this.editedUser.debitamount = 0
            this.editedUser.creditamount = parseFloat(this.amount).toFixed(2)
          }
          this.editedUser.estateId = this.users[0].estateId
          this.editedUser.createdBy = this.$session.get('user').userId
         // console.log(this.editedUser)
          this.axios.post(this.nodeApi + '/addTransaction',this.editedUser).then((response) => {
          // console.log(response.data)
           if(response.data.affectedRows > 0){
             this.$swal.fire({
              type: 'success',
              title: 'Transaction Added',
              text: 'Your new transaction was successfully added.'
            })
            this.initialize(this.personId)
              this.amount = ''
           }else{
             this.$swal.fire({
              type: 'error',
              title: 'Adding Transaction Failed',
              text: response.data.originalError.info.message
            })
           }
           
          }).catch(err =>{
            this.$swal.fire({
              type: 'error',
              title: 'Adding Transaction Failed',
              text: err
            })
          })
          this.close()
        }
        }
        
      },

      
      saveEstate () {
        
       
        if (this.estateIndex > -1) {
         
        let data = {
          estateName: this.estate.estateName,
          estateValue: this.estate.estateValue,
          estateId: this.estate.estateId
        }
        
          this.axios.post(this.nodeApi + '/updateEstate',data).then((response) => {
         
           if(response.data.affectedRows > 0){
             this.$swal.fire({
              type: 'success',
              title: 'Estate Updated',
              text: 'Your estate was successfully updated.'
            })
            this.getPerson(this.personId)
            this.initialize(this.personId)
           }else{
             this.$swal.fire({
              type: 'error',
              title: 'Updating Estate Failed',
              text: response.data.originalError.info.message
            })
           }
           
          }).catch(err =>{
            this.$swal.fire({
              type: 'error',
              title: 'Updating Estate Failed',
              text: err
            })
          })
          this.closeEstate()
        } else {
          if(!this.estate.estateName){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Estate Failed',
              text: 'Please enter a valid Estate Name'
            })
          }else if(!this.estate.estateValue){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Estate Failed',
              text: 'Please enter a valid Estate Value'
            })
          }else{

          this.estate.deathRecordId = this.personId
       
          this.estate.createdBy = this.$session.get('user').userId
       //   console.log(this.estate)
          this.axios.post(this.nodeApi + '/addEstate',this.estate).then((response) => {
          // console.log(response.data)
           if(response.data.affectedRows > 0){
             this.$swal.fire({
              type: 'success',
              title: 'Estate Added',
              text: 'Your new estate was successfully added.'
            })
            this.getPerson(this.personId)
            this.initialize(this.personId)
           }else{
             this.$swal.fire({
              type: 'error',
              title: 'Adding estate Failed',
              text: response.data.originalError.info.message
            })
           }
           
          }).catch(err =>{
            this.$swal.fire({
              type: 'error',
              title: 'Adding estate Failed',
              text: err
            })
          })
          this.closeEstate()
         }
         }
        
      },
    },
  
    
}
</script>
<style scoped>

</style>