<script>
import Dashboard from '@/components/Dashboard'
import UserAdmin from '@/components/UserAdmin'
import Estates from '@/components/Estates'
import Financials from '@/components/Financials'
import Beneficiaries from '@/components/Beneficiaries'
import Persons from '@/components/Persons'


export default {
  components: {
    
    'dashboard':Dashboard,
    'useradmin':UserAdmin,
    'estates':Estates,
    'financials':Financials,
    'beneficiaries':Beneficiaries,
    'persons':Persons
  },
  data() {
    return {
      nodeApi: '',
      userName:'',
      userType:null,
      page:'',
      drawer: false,
      dialogloader: false,
    }
  },
  
  mounted(){
    if(this.$session.exists()){
        
        this.userName = this.$session.get('user').emailAddress
        this.userType = this.$session.get('user').usertype
       
        const Toast = this.$swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000
          });

          Toast.fire({
            type: 'success',
            title: 'Signed in successfully'
          })
       
      }else{
         this.$router.push({name:'Login'}).catch(()=>{});
      
      }
      if(this.userType > 2){
        this.page = 'persons'
      }else{
        this.page = 'persons'
      }
      
  },
  methods: {
    Headering(data){
      if(data == 'useradmin'){
        return 'User Admin Page'
      }
      if(data == 'estates'){
        return 'Estates Page'
      }
       if(data == 'beneficiaries'){
        return 'Beneficiaries Page'
      }
      if(data == 'financials'){
        return 'Financials Page'
      }
      if(data == 'persons'){
        return 'Persons Page'
      }
      if(data == 'dashboard'){
        return 'Dashboard Page'
      }
    },
    logout(){
     this.$session.destroy() 
     this.$router.push({name:'Login'}).catch(()=>{});
        
    },
    selectMenu(data){
         this.page = ''
         this.page = data
         this.drawer = false
    }
  },
}
</script>