<template>
  <div class="echarts">
    <IEcharts
      :option="bar"
      
      @ready="onReady"
      @click="onClick"
    />
    
  </div>
</template>

<script type="text/babel">
  import IEcharts from 'vue-echarts-v3/src/full.js';
  export default {
    name: 'view',
    components: {
      IEcharts
    },
    props: {
    },
    data: () => ({
      loading: true,
      bar: {
    tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
    },
    legend: {
        bottom: 10,
        left: 'center',
        data: ['Teller 1', 'Teller 2', 'Teller 3', 'Teller 4', 'Teller 5']
    },
    series: [
        {
            name: 'Financials',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
                show: false,
                position: 'center'
            },
            emphasis: {
                label: {
                    show: true,
                    fontSize: '30',
                    fontWeight: 'bold'
                }
            },
            labelLine: {
                show: false
            },
            data: [
                {value: 335, name: 'Teller 1'},
                {value: 310, name: 'Teller 2'},
                {value: 234, name: 'Teller 3'},
                {value: 135, name: 'Teller 4'},
                {value: 1548, name: 'Teller 5'}
            ]
        }
    ]
}
    }),
    methods: {
      doRandom() {
        const that = this;
        let data = [];
        for (let i = 0, min = 5, max = 99; i < 6; i++) {
          data.push(Math.floor(Math.random() * (max + 1 - min) + min));
        }
        that.loading = !that.loading;
        that.bar.series[0].data = data;
      },
      onReady(instance, ECharts) {
        console.log(instance, ECharts);
      },
      onClick(event, instance, ECharts) {
        console.log(event,instance,ECharts);
       
      }
    }
  };
</script>

<style scoped>
  .echarts {
    width: 500px;
    height: 600px;
  }
</style>