<template>
<div class="text-right" style="margin-top:10px;">

    <label class="btn btn-default">
        <v-chip
        v-for="(item,i) in estateAttachments"
        :key="i"
        class="ma-2"
        large
        :color="colors[item.docType-1]"
        outlined
        label
        @click="openAttachment(item)"
        >
        <v-icon left>
            attachment
        </v-icon>
        {{item.docTypeName}}
        </v-chip>
    </label>
    <br><br>
    <!-- <v-btn :disabled="!selectedFiles" @click="upload('1')" color="red" dark class="ma-5">FD 15</v-btn>
    <v-btn :disabled="!selectedFiles" @click="upload('2')" color="red" dark class="ma-5">FD 16</v-btn>
    <br><br> -->
    <v-data-table :headers="headers" :items="users" :search="search" sort-by="calories" class="elevation-2">
        <template v-slot:top>
            <v-toolbar flat color="white">
                <v-spacer></v-spacer>
                       <v-btn v-if="estateStatusCheck != 8" @click="dialogUpload = true"  color="error" dark class="mb-2 mr-5" >Add Attachment</v-btn>
                       
                        <v-btn v-if="estateStatusCheck >= 7" @click="Upload2"  color="green" dark class="mb-2 mr-5">
                            <v-icon left>
                                attachment
                            </v-icon>
                            Add Tier 2
                        </v-btn>
                          <v-btn  color="primary" dark class="mb-2" >New Beneficiary</v-btn>
                       
              
            </v-toolbar>
        </template>
        
        <template v-slot:item.estateStatus="{ item }">
            <v-chip
            v-if="item.estateStatus == 5"
            class="ma-2"
            color="pink"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>

             <v-chip
             v-if="item.estateStatus == 6"
            class="ma-2"
            color="cyan"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>

            <v-chip
            v-if="item.estateStatus == 7"
            class="ma-2"
            color="blue"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>

             <v-chip
             v-if="item.estateStatus == 8"
            class="ma-2"
            color="green"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>
            
        </template>
        <template v-slot:item.estateValue="{ item }">
            ${{ parseFloat(item.estateValue).toFixed(2) }}
        </template>
        <template v-slot:item.createdAt="{ item }">
            {{
          new Date(item.createdAt).toLocaleString("en-GB", { timeZone: "UTC" })
        }}
        </template>
        <template v-slot:item.updatedAt="{ item }">
            {{
          new Date(item.updatedAt).toLocaleString("en-GB", { timeZone: "UTC" })
        }}
        </template>
        <template v-slot:item.actions="{ item }">
            <v-icon color="blue" class="mr-2" @click="editEstate(item)" v-if="userType <= 2">
                edit
            </v-icon>

            <v-icon color="red" class="mr-2" @click="deleteEstate(item)" v-if="userType == 1">
                delete
            </v-icon>
        </template>
        <template v-slot:no-data>
            <v-btn color="primary" dark class="mb-2" @click="newEstate">New Estate</v-btn>
        </template>
    </v-data-table>

    <br />
    <br />
    <v-data-table :headers="headersBene" :items="beneficiaries" :search="searchBene" sort-by="beneficiaryId" class="elevation-2">
        <template v-slot:top>
            <v-toolbar flat color="white">
                <v-spacer></v-spacer>
                <v-text-field dense v-model="searchBene" append-icon="search" label="Search Beneficiaries" solo class="mt-5 mr-5"></v-text-field>
            </v-toolbar>
        </template>

        <template v-slot:item.createdAt="{ item }">
            {{
          new Date(item.createdAt).toLocaleString("en-GB", { timeZone: "UTC" })
        }}
        </template>
        <template v-slot:item.updatedAt="{ item }">
            {{
          new Date(item.updatedAt).toLocaleString("en-GB", { timeZone: "UTC" })
        }}
        </template>
        <template v-slot:item.actions="{ item }">
            <v-icon color="blue" class="mr-2" @click="editItem(item)" v-if="userType <= 2">
                edit
            </v-icon>

            <v-icon color="red" class="mr-2" @click="deleteItem(item)" v-if="userType == 1">
                delete
            </v-icon>
        </template>
        <template v-slot:no-data>
            <h2>No beneficiaries found for this estate</h2>
        </template>
    </v-data-table>

    <v-dialog v-model="dialogEstate" max-width="500px">
        <v-card>
            <v-card-title class="headline green lighten-2 white--text" primary-title>
                <span class="headline">{{ formTitleEstate }} For {{ firstName }} {{ lastName }}</span>
            </v-card-title>

            <v-card-text>
                <v-container>
                    <v-row>
                        <v-col cols="12" sm="12" md="12">
                            <v-text-field v-model="estate.estateName" label="Estate Name" :placeholder="`${firstName} ${lastName} Trust`"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                            <v-text-field v-model="estate.estateValue" label="Estate Value" type="number" placeholder="0.00" prefix="$"></v-text-field>
                        </v-col>
                    </v-row>
                </v-container>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeEstate">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="saveEstate">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>


    <v-dialog v-model="dialogUpload" max-width="500px">
        <v-card>
            <v-card-title class="headline green lighten-2 white--text" primary-title>
                <span class="headline">{{ formTitleEstateTier }} </span>
            </v-card-title>

            <v-card-text>
                <div style="text-align:left;margin-top:20px;">
                    <v-row>
                        <v-col cols="12" sm="12" md="12">
                           <input type="file" ref="file" @change="selectFile" />
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                            <v-combobox
                            v-model="docTypeName"
                            :items="docType"
                            item-text="docName"
                            item-value="id"
                            label="Select Document Type"
                            ></v-combobox>
                        </v-col>
                    </v-row>
                </div>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogUpload = false">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="upload">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogUpload2" max-width="500px">
        <v-card>
            <v-card-title class="headline blue lighten-2 white--text" primary-title>
                <span class="headline">{{ formTitleEstateTier }} </span>
            </v-card-title>

            <v-card-text>
                <div style="text-align:left;margin-top:20px;">
                    <v-row>
                        <v-col cols="12" sm="12" md="12">
                           <input type="file" ref="file" @change="selectFile" />
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                            <v-combobox
                            v-model="docTypeName"
                            :items="docType"
                            item-text="docName"
                            item-value="id"
                            label="Select Document Type"
                            ></v-combobox>
                        </v-col>
                    </v-row>
                </div>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogUpload2 = false">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="upload2">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>


      <v-dialog v-model="dialog" max-width="500px">
                    
                    <v-card>
                        <v-card-title class="headline blue lighten-2 white--text" primary-title>
                            <span class="headline">{{ formTitle }} For {{ estateName }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedUser.firstName" label="First Name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedUser.lastName" label="Last Name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedUser.nationalId" label="National ID"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedUser.stake" label="Percentage Stake" placeholder="100" type="number" suffix="%"></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
</div>
</template>

<script>
export default {
    props: {
        personId: Number,
    },
    data() {
        return {
            nodeApi: "",
            selectedFiles: undefined,
            currentFile: undefined,
            progress: 0,
            colors:['green','blue','purple'],
            message: "",
            docTypeName:'',
            estateId:null,
            dialogUpload:false,
            dialogUpload2:false,
            fileInfos: [],
            formTitleEstateTier: '',
            page: "Main Page",
            dialog: false,
            dialogEstate: false,
            search: "",
            searchBene: "",
            firstName: "",
            lastName: "",
            estateName: "",
            estateStatusCheck:5,
            confirm_password: "",
            userTypeData: [{
                    id: 1,
                    name: "Registrar"
                },
                {
                    id: 2,
                    name: "Court Officer"
                },
                {
                    id: 3,
                    name: "Judge"
                },
                {
                    id: 4,
                    name: "Lawyer"
                },
                {
                    id: 5,
                    name: "Prisons Officer"
                },
            ],
            userStatusData: [{
                    id: 1,
                    name: "Active"
                },
                {
                    id: 2,
                    name: "Inactive"
                },
            ],
            headers: [{
                    text: "Estate ID",
                    value: "estateId"
                },
                {
                    text: "Estate Name",
                    value: "estateName"
                },
                {
                    text: "Full Estate Value",
                    value: "estateValue"
                },
                {
                    text: "Estate Owner",
                    value: "estateOwner"
                },
                {
                    text: "Estate Status",
                    value: "estateStatus"
                },
                {
                    text: "Date Registered",
                    value: "createdAt"
                },
                {
                    text: "Date Modified",
                    value: "updatedAt"
                },
                {
                    text: "Actions",
                    value: "actions",
                    sortable: false
                },
            ],
            headersBene: [{
                    text: "Beneficiary ID",
                    value: "beneficiaryId"
                },
                {
                    text: "First Name",
                    value: "firstName"
                },
                {
                    text: "Last Name",
                    value: "lastName"
                },
                {
                    text: "Nation ID",
                    value: "nationalId"
                },
                {
                    text: "Estate Stake",
                    value: "stake"
                },
                {
                    text: "Date Registered",
                    value: "createdAt"
                },
                {
                    text: "Date Modified",
                    value: "updatedAt"
                },
                {
                    text: "Actions",
                    value: "actions",
                    sortable: false
                },
            ],
            users: [],
            person: [],
            editedIndex: -1,
            editedUser: {
                estateId: "",
                firstName: "",
                lastName: "",
                nationalId: "",
                stake: "",
            },
            defaultUser: {
                estateId: "",
                firstName: "",
                lastName: "",
                nationalId: "",
                stake: "",
            },
            estateIndex: -1,
            estate: {
                estateName: "",
                estateValue: "",
                deathRecordId: "",
            },
            defaultEstate: {
                estateName: "",
                estateValue: "",
                deathRecordId: "",
            },
            disabled: true,
            beneficiaries: [],
            docType:[],
            disabledBene: false,
            userType: null,
            docTier:null,
            estateAttachments:[],
            saveTier1Docs:[],
        };
    },
    computed: {
        formTitle() {
            return this.editedIndex === -1 ? "New Beneficiary" : "Edit Beneficiary";
        },
        formTitleEstate() {
            return this.estateIndex === -1 ? "New Estate" : "Edit Estate";
        },
        
    },

    watch: {
        dialog(val) {
            val || this.close();
        },
        docTypeName(val){
            console.log(val)
        },
        dialogUpload(val){
            if(val){
                this.selectedFiles = undefined
                this.currentFile = undefined
                this.docTypeName = ''
                //console.log(this.docTier)
               // this.initialize(this.personId)
               if(this.docTier == 3){
                  // this.initialize2(this.personId)
               }
               // this.saveCheck2()
                
               
            }
        },
         dialogUpload2(val){
            if(val){
                this.selectedFiles = undefined
                this.currentFile = undefined
                this.docTypeName = ''
                //console.log(this.docTier)
                
                
               
            }
        }
    },
    mounted() {
        this.userType = this.$session.get('user').usertype
        this.getPerson(this.personId);
        this.initialize(this.personId);
        
    },

    created() {
        //this.initialize()
    },

    methods: {
        Upload2(){
               // this.docTier = 2
                this.estateDocNames(2)
                this.saveCheck3()
                this.formTitleEstateTier = 'Tier 2 Document Upload Form'
                this.dialogUpload2 = true
        },
        openAttachment(data){
           this.url = `http://192.168.100.15:3000/getDoc/${data.docName}`
           window.open(this.url);
        },
        getEstateId(data){
            
             this.axios
                .post(this.nodeApi + "/updateEstateStatus", {
                    estateId: this.users[0].estateId,
                    estateStatus: data
                })
                .then((response) => {
                    console.log(response.data);
                     this.getPerson(this.personId);
                    this.initialize(this.personId);
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        selectFile() {
            this.selectedFiles = this.$refs.file.files;
            console.log(this.selectedFiles)
        },
        upload() {
            if(!this.selectedFiles){
                this.$swal.fire({
                            type: "error",
                            title: "File Upload Failed",
                            text: "Please select the document you wish to attach from you computer.",
                        });
            }
            else if(this.docTypeName == ''){
                this.$swal.fire({
                            type: "error",
                            title: "File Upload Failed",
                            text: "Please select the category of the document you selected",
                        });
            }else{
                  this.currentFile = this.selectedFiles.item(0);
            this.progress = 0;
            let formData = new FormData();

            formData.append("file", this.currentFile);
            let headers = {
                "Content-Type": "multipart/form-data"
            }
            this.axios
                .post(this.nodeApi + "/upload", formData, {
                    headers: headers
                })
                .then((response) => {
                    if (response.data.message) {

                        this.saveDocument(response.data.filename, this.docTier)
                    } else {
                        this.$swal.fire({
                            type: "error",
                            title: "File Upload Failed",
                            text: response.data.error,
                        });
                    }
                    //console.log(response.data)

                })
                .catch((err) => {
                    console.log(err);
                });

            
            }
          
        },
         upload2() {
            if(!this.selectedFiles){
                this.$swal.fire({
                            type: "error",
                            title: "File Upload Failed",
                            text: "Please select the document you wish to attach from you computer.",
                        });
            }
            else if(this.docTypeName == ''){
                this.$swal.fire({
                            type: "error",
                            title: "File Upload Failed",
                            text: "Please select the category of the document you selected",
                        });
            }else{
                  this.currentFile = this.selectedFiles.item(0);
            this.progress = 0;
            let formData = new FormData();

            formData.append("file", this.currentFile);
            let headers = {
                "Content-Type": "multipart/form-data"
            }
            this.axios
                .post(this.nodeApi + "/upload", formData, {
                    headers: headers
                })
                .then((response) => {
                    if (response.data.message) {

                        this.saveDocument2(response.data.filename, 2)
                    } else {
                        this.$swal.fire({
                            type: "error",
                            title: "File Upload Failed",
                            text: response.data.error,
                        });
                    }
                    //console.log(response.data)

                })
                .catch((err) => {
                    console.log(err);
                });

            
            }
          
        },
        saveDocument(file, filetype) {
            console.log(this.docTypeName)
            let data = {
                estateId: this.users[0].estateId,
                docName: file,
                docType: filetype,
                docTypeName:this.docTypeName.docName,
                createdBy: this.$session.get("user").userId
            }
            //  console.log(this.editedUser)
            this.axios
                .post(this.nodeApi + "/addEstateDoc", data)
                .then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.selectedFiles = undefined;
                        this.dialogUpload = false
                        this.dialogUpload2 = false
                        this.$swal.fire({
                            type: "success",
                            title: "Document Added",
                            text: "Your new estate document was successfully added.",
                        });

                        if(this.docTier == 1){
                            this.checkAllDocs(this.users[0].estateId)
                           
                        }
                        if(this.docTier == 2){
                           // this.checkAllDocs2(this.users[0].estateId)
                            this.getEstateId(7)
                        }
                        if(this.docTier == 3){
                            this.checkAllDocs3(this.users[0].estateId)
                           // this.getEstateId(8)
                        }
                        
                    } else {
                        this.$swal.fire({
                            type: "error",
                            title: "Adding Document Failed",
                            text: response.data.originalError.info.message,
                        });
                    }
                })
                .catch((err) => {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Document Failed",
                        text: err
                    });
                });
        },
         saveDocument2(file, filetype) {
            console.log(this.docTypeName)
            let data = {
                estateId: this.users[0].estateId,
                docName: file,
                docType: filetype,
                docTypeName:this.docTypeName.docName,
                createdBy: this.$session.get("user").userId
            }
            //  console.log(this.editedUser)
            this.axios
                .post(this.nodeApi + "/addEstateDoc", data)
                .then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.selectedFiles = undefined;
                        this.dialogUpload = false
                        this.dialogUpload2 = false
                        this.$swal.fire({
                            type: "success",
                            title: "Document Added",
                            text: "Your new estate document was successfully added.",
                        });

                         this.getEstateId(7)
                        
                    } else {
                        this.$swal.fire({
                            type: "error",
                            title: "Adding Document Failed",
                            text: response.data.originalError.info.message,
                        });
                    }
                })
                .catch((err) => {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Document Failed",
                        text: err
                    });
                });
        },
        saveCheck2(){
             if(this.docTier == 1){
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck", {
                    estateId: this.users[0].estateId
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                   
                    let tempdocType = this.docType
                            this.saveTier1Docs.forEach((element1) => {
                                tempdocType.forEach((element2,ii) => {
                                    if(element1.docTypeName == element2.docName){
                                        
                                        tempdocType.splice(ii, 1)
                                    }
                                });
                            });
                           
                            this.docType = tempdocType
                            console.log(this.docType)
                })
                .catch((err) => {
                    console.log(err);
                });

             }

            if(this.docTier == 2){
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck2", {
                    estateId: this.users[0].estateId
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                   
                    let tempdocType = this.docType
                            this.saveTier1Docs.forEach((element1) => {
                                tempdocType.forEach((element2,ii) => {
                                    if(element1.docTypeName == element2.docName){
                                        
                                        tempdocType.splice(ii, 1)
                                    }
                                });
                            });
                           
                            this.docType = tempdocType
                   
                })
                .catch((err) => {
                    console.log(err);
                });

             }

             if(this.docTier == 3){
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck3", {
                    estateId: this.users[0].estateId
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                   
                    let tempdocType = this.docType
                            this.saveTier1Docs.forEach((element1) => {
                                tempdocType.forEach((element2,ii) => {
                                    if(element1.docTypeName == element2.docName){
                                        
                                        tempdocType.splice(ii, 1)
                                    }
                                });
                            });
                           
                            this.docType = tempdocType
                   
                })
                .catch((err) => {
                    console.log(err);
                });

             }
            
        },
         saveCheck3(){
            

           
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck2", {
                    estateId: this.users[0].estateId
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                   
                    let tempdocType = this.docType
                            this.saveTier1Docs.forEach((element1) => {
                                tempdocType.forEach((element2,ii) => {
                                    if(element1.docTypeName == element2.docName){
                                        
                                        tempdocType.splice(ii, 1)
                                    }
                                });
                            });
                           
                            this.docType = tempdocType
                   
                })
                .catch((err) => {
                    console.log(err);
                });

             

             
            
        },
         checkAllDocs(id){
            // this.getEstateId(6)
            
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck", {
                    estateId: id
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                    //console.log(this.saveTier1Docs)
                    if(this.saveTier1Docs.length >= 6){
                        this.getEstateId(6)
                    }else{
                        this.getPerson(this.personId);
                        this.initialize(this.personId);
                    }
                })
                .catch((err) => {
                    console.log(err);
                });

        },
         checkAllDocs2(id){
            // this.getEstateId(6)
            
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck2", {
                    estateId: id
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                    //console.log(this.saveTier1Docs)
                    if(this.saveTier1Docs.length >= 3){
                        this.getEstateId(7)
                    }else{
                        this.getPerson(this.personId);
                        this.initialize(this.personId);
                    }
                })
                .catch((err) => {
                    console.log(err);
                });

        },
        checkAllDocs3(id){
            // this.getEstateId(6)
            
             this.axios
                .post(this.nodeApi + "/getEstateDocCheck3", {
                    estateId: id
                })
                .then((response) => {
                    this.saveTier1Docs = response.data;
                    //console.log(this.saveTier1Docs)
                    if(this.saveTier1Docs.length >= 3){
                        this.getEstateId(8)
                    }else{
                        this.getPerson(this.personId);
                        this.initialize(this.personId);
                    }
                })
                .catch((err) => {
                    console.log(err);
                });

        },
        newEstate() {
            this.dialogEstate = true;
        },
       async initialize(id) {
            this.axios
                .post(this.nodeApi + "/getEstate", {
                    personId: id
                })
                .then((response) => {
                    this.users = response.data;
                    if (this.users.length < 1) {
                        this.disabled = true;
                    } else {
                        this.disabled = false;
                        this.estateName = this.users[0].estateName;
                        this.estateStatusCheck = this.users[0].estateStatus;
                        this.estateDocNamesAttachment(this.users[0].estateId)
                        this.initializeBene(this.users[0].estateId);
                       // this.checkAllDocs(this.users[0].estateId);
                       //console.log(this.estateStatusCheck)
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        async initialize2(id) {
          await  this.axios
                .post(this.nodeApi + "/getEstate", {
                    personId: id
                })
                .then((response) => {
                    this.users = response.data;
                    if (this.users.length < 1) {
                        this.disabled = true;
                    } else {
                        this.disabled = false;
                        this.estateName = this.users[0].estateName;
                        this.estateStatusCheck = this.users[0].estateStatus;
                      //  this.estateDocNamesAttachment(this.users[0].estateId)
                        //this.initializeBene(this.users[0].estateId);
                       // this.checkAllDocs(this.users[0].estateId);
                       //console.log(this.estateStatusCheck)
                    }
                })
                .catch((err) => {
                    console.log(err);
                });

           await this.saveCheck2()
        },

        initializeBene(id) {
            this.axios
                .post(this.nodeApi + "/getBeneficiary", {
                    estateId: id
                })
                .then((response) => {
                    this.beneficiaries = response.data;
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        estateDocNames(data) {
            this.axios
                .post(this.nodeApi + "/getEstateDocNames", {
                    docTier: data
                })
                .then((response) => {
                    this.docType = response.data;
                })
                .catch((err) => {
                    console.log(err);
                }); 
        },
        estateDocNamesAttachment(data) {
            this.axios
                .post(this.nodeApi + "/getEstateDocNamesAttachment", {
                    estateId: data
                })
                .then((response) => {
                    
                    this.estateAttachments = response.data;
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        getPerson(id) {
            this.axios
                .post(this.nodeApi + "/getPerson", {
                    personId: id
                })
                .then((response) => {
                    this.person = response.data;
                    if (this.person.length > 0) {
                        this.firstName = this.person[0].firstName;
                        this.lastName = this.person[0].lastName;
                    }
                    // console.log(this.person)
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        estateStatus(item) {
            if (item == 5) {
                this.docTier = 1
                this.estateDocNames(this.docTier)
                this.formTitleEstateTier = 'Tier 1 Document Upload Form'
                return "New Estate Pending Upload";
            }
            if (item == 6) {
                this.docTier = 2
                this.estateDocNames(this.docTier)
                this.formTitleEstateTier = 'Tier 2 Document Upload Form'
                return "Estate @ 33%";
            }
            if (item == 7) {
                this.estateDocNames(this.docTier)
                this.formTitleEstateTier = 'Tier 3 Document Upload Form'
                this.docTier = 3
                return "Estate @ 66%";
            }
            if (item == 8) {
                return "Estate completed @ 100%";
            }
        },

        editItem(item) {
            this.editedIndex = this.beneficiaries.indexOf(item);
            this.editedUser = Object.assign({}, item);
            this.dialog = true;
        },

        editEstate(item) {
            this.estateIndex = this.users.indexOf(item);
            this.estate = Object.assign({}, item);
            this.dialogEstate = true;
        },

        deleteItem(item) {
            const index = this.beneficiaries.indexOf(item);
            confirm("Are you sure you want to delete this item?") &&
                this.beneficiaries.splice(index, 1);
        },

        deleteEstate(item) {
            const index = this.users.indexOf(item);
            confirm("Are you sure you want to delete this item?") &&
                this.users.splice(index, 1);
        },

        close() {
            this.dialog = false;

            this.$nextTick(() => {
                this.editedUser = Object.assign({}, this.defaultUser);
                this.editedIndex = -1;
            });
        },

        closeEstate() {
            this.dialogEstate = false;

            this.$nextTick(() => {
                this.estate = Object.assign({}, this.defaultEstate);
                this.estateIndex = -1;
            });
        },

        save() {
            if (this.editedIndex > -1) {
                let data = {
                    firstName: this.editedUser.firstName,
                    lastName: this.editedUser.lastName,
                    nationalId: this.editedUser.nationalId,
                    beneficiaryId: this.editedUser.beneficiaryId,
                    stake: this.editedUser.stake,
                };

                this.axios
                    .post(this.nodeApi + "/updateBeneficiary", data)
                    .then((response) => {
                        if (response.data.affectedRows > 0) {
                            this.$swal.fire({
                                type: "success",
                                title: "Beneficiary Updated",
                                text: "Your beneficiary was successfully updated.",
                            });

                            this.initialize(this.personId);
                        } else {
                            this.$swal.fire({
                                type: "error",
                                title: "Updating Beneficiary Failed",
                                text: response.data.originalError.info.message,
                            });
                        }
                    })
                    .catch((err) => {
                        this.$swal.fire({
                            type: "error",
                            title: "Updating Beneficiary Failed",
                            text: err,
                        });
                    });
                this.close();
            } else {
                if (!this.editedUser.firstName) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Beneficiary Failed",
                        text: "Please enter a valid First Name",
                    });
                } else if (!this.editedUser.lastName) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Beneficiary Failed",
                        text: "Please enter a valid Last Name",
                    });
                } else if (!this.editedUser.nationalId) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Beneficiary Failed",
                        text: "Please enter a valid National ID",
                    });
                } else if (!this.editedUser.stake) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Beneficiary Failed",
                        text: "Please enter a valid Stake percentage",
                    });
                } else if (this.editedUser.stake > 100) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Beneficiary Failed",
                        text: "Please enter a stake value less than 100%",
                    });
                } else if (this.editedUser.stake < 1) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Beneficiary Failed",
                        text: "Please enter a stake value greater than 0%",
                    });
                } else {
                    this.editedUser.estateId = this.users[0].estateId;
                    this.editedUser.createdBy = this.$session.get("user").userId;
                    //  console.log(this.editedUser)
                    this.axios
                        .post(this.nodeApi + "/addBeneficiary", this.editedUser)
                        .then((response) => {
                            // console.log(response.data)
                            if (response.data.affectedRows > 0) {
                                this.$swal.fire({
                                    type: "success",
                                    title: "Beneficiary Added",
                                    text: "Your new beneficiary was successfully added.",
                                });
                                this.initialize(this.personId);
                            } else {
                                this.$swal.fire({
                                    type: "error",
                                    title: "Adding Beneficiary Failed",
                                    text: response.data.originalError.info.message,
                                });
                            }
                        })
                        .catch((err) => {
                            this.$swal.fire({
                                type: "error",
                                title: "Adding Beneficiary Failed",
                                text: err,
                            });
                        });
                    this.close();
                }
            }
        },

        saveEstate() {
            if (this.estateIndex > -1) {
                let data = {
                    estateName: this.estate.estateName,
                    estateValue: this.estate.estateValue,
                    estateId: this.estate.estateId,
                };

                this.axios
                    .post(this.nodeApi + "/updateEstate", data)
                    .then((response) => {
                        if (response.data.affectedRows > 0) {
                            this.$swal.fire({
                                type: "success",
                                title: "Estate Updated",
                                text: "Your estate was successfully updated.",
                            });
                            this.getPerson(this.personId);
                            this.initialize(this.personId);
                        } else {
                            this.$swal.fire({
                                type: "error",
                                title: "Updating Estate Failed",
                                text: response.data.originalError.info.message,
                            });
                        }
                    })
                    .catch((err) => {
                        this.$swal.fire({
                            type: "error",
                            title: "Updating Estate Failed",
                            text: err,
                        });
                    });
                this.closeEstate();
            } else {
                if (!this.estate.estateName) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Estate Failed",
                        text: "Please enter a valid Estate Name",
                    });
                } else if (!this.estate.estateValue) {
                    this.$swal.fire({
                        type: "error",
                        title: "Adding Estate Failed",
                        text: "Please enter a valid Estate Value",
                    });
                } else {
                    this.estate.deathRecordId = this.personId;

                    this.estate.createdBy = this.$session.get("user").userId;
                    //   console.log(this.estate)
                    this.axios
                        .post(this.nodeApi + "/addEstate", this.estate)
                        .then((response) => {
                            // console.log(response.data)
                            if (response.data.affectedRows > 0) {
                                this.$swal.fire({
                                    type: "success",
                                    title: "Estate Added",
                                    text: "Your new estate was successfully added.",
                                });
                                this.getPerson(this.personId);
                                this.initialize(this.personId);
                            } else {
                                this.$swal.fire({
                                    type: "error",
                                    title: "Adding estate Failed",
                                    text: response.data.originalError.info.message,
                                });
                            }
                        })
                        .catch((err) => {
                            this.$swal.fire({
                                type: "error",
                                title: "Adding estate Failed",
                                text: err,
                            });
                        });
                    this.closeEstate();
                }
            }
        },
    },
};
</script>

