<template>
  
 
 
    <div style="margin-top:10px;">
    
    <v-data-table
        :headers="headers"
        :items="users"
        :search="search"
        sort-by="userId"
        class="elevation-1"
        
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
            <!-- <v-toolbar-title >User Administration</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider> -->
            <v-spacer></v-spacer>
            <v-text-field
              dense
              v-model="search"
              append-icon="search"
              label="Search"
              solo
              class="mt-5 mr-5"
            ></v-text-field>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >New Person</v-btn>
              </template>
              <v-card>
                <v-card-title class="headline blue lighten-2 white--text" primary-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field v-model="editedUser.firstName" label="First Name"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="6">
                        <v-text-field v-model="editedUser.lastName" label="Last Name"></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field v-model="editedUser.deathRecordId" label="Death Record Number"></v-text-field>
                      </v-col>
                       
                      
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.createdAt="{ item }">
            {{new Date(item.createdAt).toLocaleString('en-GB', { timeZone: 'UTC' }) }}
        </template>
         <template v-slot:item.updatedAt="{ item }">
            {{new Date(item.updatedAt).toLocaleString('en-GB', { timeZone: 'UTC' }) }}
        </template>
        <template v-slot:item.estateStatus="{ item }">
            <v-chip
            v-if="item.estateStatus == 5"
            class="ma-2"
            color="pink"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>

             <v-chip
             v-if="item.estateStatus == 6"
            class="ma-2"
            color="cyan"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>

            <v-chip
            v-if="item.estateStatus == 7"
            class="ma-2"
            color="blue"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>

             <v-chip
             v-if="item.estateStatus == 8"
            class="ma-2"
            color="green"
            label
            text-color="white"
            >
            <v-icon left>
                label
            </v-icon>
            {{ estateStatus(item.estateStatus) }}
            </v-chip>
            
        </template>
        <template v-slot:item.actions="{ item }">

           <v-tooltip bottom v-if="userType <= 2">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs" v-on="on"
                color="blue"
                class="mr-2"
                @click="editItem(item)"
              >
                edit
              </v-icon>
           </template>
          <span>Edit Person</span>
        </v-tooltip>
          
          <v-tooltip bottom v-if="userType == 4 || userType <= 2">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs" v-on="on"
                color="red"
                class="mr-2"
                @click="openSub(item.personId,'View Estates Page','estates')"
              >
                business
              </v-icon>
           </template>
          <span>View Estates</span>
        </v-tooltip>

        <v-tooltip bottom v-if="userType == 3 || userType <= 2">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs" v-on="on"
                color="green"
                class="mr-2"
               @click="openSub(item.personId,'View Financials Page','financials')"
              >
                attach_money
              </v-icon>
           </template>
          <span>View Financials</span>
        </v-tooltip>

        <!-- <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs" v-on="on"
                color="purple"
                class="mr-2"
               @click="openSub(item.personId,'View Beneficiaries Page','beneficiaries')"
              >
                supervised_user_circle
              </v-icon>
           </template>
          <span>View Beneficiaries</span>
        </v-tooltip> -->
        
        <!-- <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs" v-on="on"
                color="gray"
                class="mr-2"
               @click="openSub(item.personId,'View Documents Page','documents')"
              >
                assignment
              </v-icon>
           </template>
          <span>View Documents</span>
        </v-tooltip> -->

         

        </template>
        <template v-slot:no-data>
          <h2>No Records Found</h2>
        </template>
      </v-data-table>


     <v-dialog v-model="dialogFull" fullscreen hide-overlay transition="dialog-bottom-transition">
      
      <v-card>
        <v-toolbar dark color="primary">
         
          <v-toolbar-title>{{subMenu}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text @click="dialogFull = false">Close</v-btn>
          </v-toolbar-items>
        </v-toolbar>

         <div style="margin-right:20px;margin-left:20px;">
         
           <component v-bind:is="subPage" v-bind:personId="selectedPersonId"></component>
         </div>
       
      </v-card>
    </v-dialog>

    </div>
    
</template>
<script>
import Estates from '@/components/Estates'
import Financials from '@/components/Financials'
import Beneficiaries from '@/components/Beneficiaries'
import Documents from '@/components/Documents'
export default {
  components: {
    
    'estates':Estates,
    'financials':Financials,
    'beneficiaries':Beneficiaries,
    'documents':Documents
  },
  data () {
    return {
      nodeApi: '',
      page : 'Main Page',
      dialog: false,
      subMenu: '',
      subPage: '',
      dialogRestPassword:false,
      dialogFull:false,
      search: '',
      confirm_password: '',
      userTypeData: [{id:1,name:'Registrar'},{id:2,name:'Court Officer'},{id:3,name:'Judge'},{id:4,name:'Lawyer'},{id:5,name:'Prisons Officer'}],
      userStatusData: [{id:1,name:'Active'},{id:2,name:'Inactive'}],
      headers: [
        { text: 'Person ID',value: 'personId'},
        { text: 'First Name', value: 'firstName' },
        { text: 'Last Name', value: 'lastName' },
        { text: 'Death Record', value: 'deathRecordId' },
        { text: 'Date Registered', value: 'createdAt' },
        { text: 'Date Modified', value: 'updatedAt' },
        { text: 'Estate Status', value: 'estateStatus' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      users: [],
      editedIndex: -1,
      
      editedUser: {
         
        firstName: '',
        lastName: '',
        deathRecordId: '',
        createdBy:''
      },
      defaultUser: {
        
        firstName: '',
        lastName: '',
        createdBy: '',
        deathRecordId:''
      },
      selectedPersonId:null,
      userType:null
      
    }
  },
  computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Person' : 'Edit Person'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogFull(val){
        if(!val){
          this.selectedPersonId = null
          this.subPage = ''
        }
      }
     
    },
    mounted(){
      this.userType = this.$session.get('user').usertype
    },
    created () {
      this.initialize()
    },

    methods: {
      
      initialize () {
        this.axios.get(this.nodeApi + '/getAllPersons').then((response) => {
         this.users = response.data
        }).catch(err =>{
         console.log(err)
        })
       
      },
     estateStatus(item) {
            if (item == 5) {
                
                return "New Estate Pending Upload";
            }
            if (item == 6) {
               
                return "Estate @ 33%";
            }
            if (item == 7) {
                
                return "Estate @ 66%";
            }
            if (item == 8) {
                return "Estate completed @ 100%";
            }
        },
     openSub(personId,header,page){
      // console.log(page)
         this.subPage = ''
         this.subMenu = header
         this.selectedPersonId = personId
         this.subPage = page
         
         this.dialogFull = true
     },
      editItem (item) {
        this.editedIndex = this.users.indexOf(item)
        this.editedUser = Object.assign({}, item)
        this.dialog = true
      },

      close () {
        this.dialog = false
        this.confirm_password = ''
        this.$nextTick(() => {
          this.editedUser = Object.assign({}, this.defaultUser)
          this.editedIndex = -1
        })
        
      },

      save () {
        
       
        if (this.editedIndex > -1) {
         
        let data = {
          
          firstName: this.editedUser.firstName,
          lastName: this.editedUser.lastName,
          deathRecordId: this.editedUser.deathRecordId,
          personId:this.editedUser.personId
          
        }
       
          this.axios.post(this.nodeApi + '/updatePerson',data).then((response) => {
         
           if(response.data.affectedRows > 0){
             this.$swal.fire({
              type: 'success',
              title: 'Person Updated',
              text: 'Your Person was successfully updated.'
            })
            this.initialize()
           }else{
             this.$swal.fire({
              type: 'error',
              title: 'Updating Person Failed',
              text: response.data.originalError.info.message
            })
           }
           
          }).catch(err =>{
            this.$swal.fire({
              type: 'error',
              title: 'Updating Person Failed',
              text: err
            })
          })
          this.close()
        } else {
          if(!this.editedUser.firstName){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Person Failed',
              text: 'Please enter a valid First Name'
            })
          }else if(!this.editedUser.lastName){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Person Failed',
              text: 'Please enter a valid Last Name'
            })
          }else if(!this.editedUser.deathRecordId){
            this.$swal.fire({
              type: 'error',
              title: 'Adding Person Failed',
              text: 'Please enter a valid Death Record Number'
            })
          }else{

        
        
          this.editedUser.createdBy = this.$session.get('user').userId
         // console.log(this.editedUser)
          this.axios.post(this.nodeApi + '/addPerson',this.editedUser).then((response) => {
           console.log(response.data)
           if(response.data.affectedRows > 0){
             this.$swal.fire({
              type: 'success',
              title: 'Person Added',
              text: 'Your new Person was successfully added.'
            })
            this.initialize()
           }else{
             this.$swal.fire({
              type: 'error',
              title: 'Adding Person Failed',
              text: response.data.originalError.info.message
            })
           }
           
          }).catch(err =>{
            this.$swal.fire({
              type: 'error',
              title: 'Adding Person Failed',
              text: err
            })
          })
          this.close()
        }
        }
        
      },
    },
  
    
}
</script>
<style scoped>

</style>