<template>
<div style="margin-top:10px;">

    <v-data-table :headers="headers" :items="users" :search="search" sort-by="calories" class="elevation-1">
        <template v-slot:top>
            <v-toolbar flat color="white">
                <!-- <v-toolbar-title >User Administration</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider> -->
                <v-spacer></v-spacer>
                <v-text-field dense v-model="search" append-icon="search" label="Search" solo class="mt-5 mr-5"></v-text-field>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">New Beneficiary</v-btn>
                    </template>
                    <v-card>
                        <v-card-title class="headline blue lighten-2 white--text" primary-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-row>

                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedUser.firstName" label="First Name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedUser.lastName" label="Last Name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="12">
                                        <v-text-field v-model="editedUser.emailAddress" label="Email Address"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6" v-if="formTitle == 'New User'">
                                        <v-text-field v-model="editedUser.userPassword" type="password" label="Password"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6" v-if="formTitle == 'New User'">
                                        <v-text-field v-model="confirm_password" type="password" label="Confirm Password"></v-text-field>
                                    </v-col>

                                </v-row>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
            <v-icon color="blue" class="mr-2" @click="editItem(item)">
                mdi-pencil
            </v-icon>

            <v-icon color="red" class="mr-2" @click="deleteItem(item)">
                mdi-delete
            </v-icon>

        </template>
        <template v-slot:no-data>
            <v-btn color="primary" @click="initialize">Reset</v-btn>
        </template>
    </v-data-table>
</div>
</template>

<script>
export default {
    props: {
        personId: Number
    },
    data() {
        return {
            nodeApi: '',
            page: 'Main Page',
            dialog: false,
            search: '',
            confirm_password: '',
            userTypeData: [{
                id: 1,
                name: 'Registrar'
            }, {
                id: 2,
                name: 'Court Officer'
            }, {
                id: 3,
                name: 'Judge'
            }, {
                id: 4,
                name: 'Lawyer'
            }, {
                id: 5,
                name: 'Prisons Officer'
            }],
            userStatusData: [{
                id: 1,
                name: 'Active'
            }, {
                id: 2,
                name: 'Inactive'
            }],
            headers: [{
                    text: 'User ID',
                    value: 'userId'
                },
                {
                    text: 'First Name',
                    value: 'firstName'
                },
                {
                    text: 'Last Name',
                    value: 'lastName'
                },
                {
                    text: 'Email Address',
                    value: 'emailAddress'
                },
                {
                    text: 'User Status',
                    value: 'status'
                },
                {
                    text: 'Actions',
                    value: 'actions',
                    sortable: false
                },
            ],
            users: [],
            editedIndex: -1,
            editedUser: {
                userName: '',
                firstName: '',
                lastName: '',
                email: ''
            },
            defaultUser: {
                userName: '',
                firstName: '',
                lastName: '',
                email: '',
                userPassword: ''
            },
        }
    },
    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New Beneficiary' : 'Edit Beneficiary'
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        },

    },
    mounted() {
        console.log(this.personId)
    },

    created() {
        this.initialize()
    },

    methods: {
        initialize() {
            this.axios.get(this.nodeApi + '/getAllUsers').then((response) => {
                this.users = response.data
            }).catch(err => {
                console.log(err)
            })

        },

        editItem(item) {
            this.editedIndex = this.users.indexOf(item)
            this.editedUser = Object.assign({}, item)
            this.dialog = true
        },

        deleteItem(item) {
            const index = this.users.indexOf(item)
            confirm('Are you sure you want to delete this item?') && this.users.splice(index, 1)
        },

        close() {
            this.dialog = false
            this.confirm_password = ''
            this.$nextTick(() => {
                this.editedUser = Object.assign({}, this.defaultUser)
                this.editedIndex = -1
            })

        },

        save() {

            if (this.editedIndex > -1) {
                this.editedUser.userType = this.editedUser.userType.id
                this.editedUser.status = this.editedUser.status.id
                let data = {
                    userName: this.editedUser.userName,
                    firstName: this.editedUser.firstName,
                    lastName: this.editedUser.lastName,
                    email: this.editedUser.email,
                    cell: this.editedUser.cell,
                    userType: this.editedUser.userType,
                    status: this.editedUser.status,
                    password: this.editedUser.password
                }
                let userId = this.editedUser.id
                this.axios.post(this.nodeApi + '/updateUser', {
                    data: data,
                    id: userId
                }).then((response) => {

                    if (response.data.affectedRows > 0) {
                        this.$swal.fire({
                            type: 'success',
                            title: 'User Updated',
                            text: 'Your user was successfully updated.'
                        })
                        this.initialize()
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Updating User Failed',
                            text: response.data.sqlMessage
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating User Failed',
                        text: err
                    })
                })
                this.close()
            } else {
                if (!this.editedUser.firstName) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid First Name'
                    })
                } else if (!this.editedUser.lastName) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Last Name'
                    })
                } else if (!this.editedUser.email) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Email Address'
                    })
                } else if (!this.editedUser.cell) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Cell Number'
                    })
                } else if (!this.editedUser.userName) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid User Name'
                    })
                } else if (!this.editedUser.password) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Password'
                    })
                } else if (this.editedUser.password != this.confirm_password) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Passwords do not match'
                    })
                } else if (!this.editedUser.userType) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid User Type'
                    })
                } else if (!this.editedUser.status) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid User Status'
                    })
                } else {

                    this.editedUser.userType = this.editedUser.userType.id
                    this.editedUser.status = this.editedUser.status.id
                    this.editedUser.createdBy = this.$session.get('user').id
                    this.axios.post(this.nodeApi + '/addUser', this.editedUser).then((response) => {
                        // console.log(response.data)
                        if (response.data.affectedRows > 0) {
                            this.$swal.fire({
                                type: 'success',
                                title: 'User Added',
                                text: 'Your new user was successfully added.'
                            })
                            this.initialize()
                        } else {
                            this.$swal.fire({
                                type: 'error',
                                title: 'Adding User Failed',
                                text: response.data.sqlMessage
                            })
                        }

                    }).catch(err => {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding User Failed',
                            text: err
                        })
                    })
                    this.close()
                }
            }

        },
    },

}
</script>

